import style from "./Item.module.css";
import React, { useState } from "react";
// import { useMarkStarAnnouncementMutation } from "../../../../../../../services/qvipleAPI";
import { imageShowUrl } from "../../../../../../../services/BaseUrl";
import moment from "moment";
import { Link, useLocation, useNavigate, useParams } from "react-router-dom";
import AnnouncementMenu from "./AnnouncementMenu";

function Item({ data, onRefetch }) {
  // const [starred, setStarred] = useState(false);
  const getQuery = useLocation();
  const navigate = useNavigate();
  const params = useParams();
  const [add, setAdd] = useState(false);

  const onNavigate = () => {
    navigate(`/q/${params.username}/notifications?a=announcement&n=detail`, {
      state: {
        ...getQuery.state,
        annId: data._id,
      },
    });
  };

  const onDefault = (e) => {
    e.stopPropagation();
  };

  const onClose = (e) => {
    e.stopPropagation();
    setAdd((pre) => !pre);
  };

  return (
    <>
      <div className={style.item} onClick={onNavigate}>
        <div className={style.head}>
          <Link to={`/q/${data?.institute?.name}/profile`} onClick={onDefault}>
            <img
              alt="Announcement"
              src={
                data?.institute.insProfilePhoto
                  ? `${imageShowUrl}/${data?.institute.insProfilePhoto}`
                  : "/images/ins_default_profile.svg"
              }
            />
          </Link>
          <div className={style.text}>
            <h6>{data && data?.institute.name}</h6>
            <p>
              {moment(data?.createdAt).format("MMM Do YY")} |{" "}
              {moment(data?.createdAt).format("LT")}
            </p>
          </div>
        </div>

        <div className={style.content}>
          <p>
            <span>{data?.insAnnTitle?.substr(0, 130)}</span>
          </p>
        </div>

        {/* {starred ? (
        <img
          className={style.bookmark}
          src="/images/bookmark-yellow.svg"
          // onClick={() => setStarred(!starred)}
          alt=""
        />
      ) : ( */}
        {/* <img
          className={style.bookmark}
          src="/images/bookmark-post.svg"
          // onClick={() => setStarred(!starred)}
          alt=""
        /> */}
        <img
          src="/images/menu-dots-icon.svg"
          alt="icon"
          className={style.bookmark}
          onClick={onClose}
        />
        {/* )} */}

        {/* {data && data.starList && data.starList.includes(id && `${id}`) ? 
      (
        <img
          className={style.bookmark}
          src="/images/bookmark-yellow.svg"
          // onClick={() => MarkStar(data?._id)}
        />
      ) : ( */}
        {/* <img
        className={style.bookmark}
        src="/images/bookmark-post.svg"
        // onClick={() => MarkStar(data?._id)}
      /> */}
        {/* )
     } */}
      </div>
      {add && (
        <AnnouncementMenu
          data={data}
          onClose={() => setAdd(false)}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
}

export default Item;
