import { useState } from "react";
import ModalMenu from "../../../../../../../optimized/Ui/Modal/ModalMenu";
import { useInstituteRemoveAnnouncementMutation } from "../../../../../../../services/qvipleAPI";
import { useTranslation } from "react-i18next";

const AnnouncementMenu = ({ data, onClose, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [instituteRemoveAnnouncement] =
    useInstituteRemoveAnnouncementMutation();

  const onRemove = () => {
    if (data?._id) {
      setDisabled((pre) => !pre);
      instituteRemoveAnnouncement({
        annId: data?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return (
    <ModalMenu onClose={onClose} disabled={disabled}>
      <h6 onClick={onRemove}>{t("delete")}</h6>
    </ModalMenu>
  );
};

export default AnnouncementMenu;
